.navigation {
  position: fixed;
  right: 0;
  top: 0;
  z-index: 2; }
  .navigation__hamburger {
    position: absolute;
    right: 30px;
    top: 30px;
    z-index: 1; }
  .navigation__menu {
    bottom: 0;
    left: 0;
    opacity: 0;
    position: absolute;
    right: 0;
    top: 0; }
  .navigation__menu--active {
    bottom: 0;
    opacity: 1; }
