.prj-summary__lead {
  background-color: #ffffff;
  padding: 2.4rem 2rem; }
.prj-summary__lead-text {
  font-size: 1.2rem;
  line-height: 1.2rem; }
.prj-summary__meta {
  background-color: #eaeaec;
  padding: 2.4rem 2rem; }
.prj-summary__meta-item {
  font-size: 0.96rem;
  line-height: 1.2rem;
  margin-bottom: 3.6rem; }
  .prj-summary__meta-item:last-child {
    margin-bottom: 0; }
@media only screen and (min-width: 540px) {
  .prj-summary__lead {
    padding-bottom: 3.6rem;
    padding-top: 3.6rem; }
  .prj-summary__lead-text {
    font-size: 1.56rem;
    line-height: 2.4rem; }
  .prj-summary__meta {
    padding-bottom: 3.6rem;
    padding-top: 3.6rem; } }
@media only screen and (min-width: 768px) {
