.prj-minimal-info__text {
  background-color: #fafafa;
  font-size: 0.96rem;
  line-height: 1.2rem;
  padding: 3.6rem 2rem; }
.prj-minimal-info__text-title {
  font-weight: bold;
  margin-bottom: 1.2rem; }
@media only screen and (min-width: 540px) {
  .prj-minimal-info__text {
    padding: 3.6rem 2rem; } }
@media only screen and (min-width: 768px) {
  .prj-minimal-info {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    height: 100vh; }
    .prj-minimal-info__text {
      align-items: flex-start;
      display: flex;
      flex-direction: column;
      font-size: 1.2rem;
      height: 50vh;
      justify-content: center;
      line-height: 1.2rem;
      padding: 0 3rem;
      width: 33.33333%; }
    .prj-minimal-info__main-image-wrapper {
      height: 100vh;
      order: 2;
      position: relative;
      width: 66.66667%; }
    .prj-minimal-info__small-image-wrapper {
      height: 50vh;
      position: relative;
      width: 33.33333%; }
    .prj-minimal-info__image {
      height: 100%;
      left: 0;
      object-fit: cover;
      position: absolute;
      top: 0;
      width: 100%; } }
@media only screen and (min-width: 1024px) {
  .prj-minimal-info__text {
    padding-left: 4rem;
    padding-right: 4rem; } }
