html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

body {
  margin: 0;
  font: 16px/1 sans-serif;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased; }

h1,
h2,
h3,
h4,
p,
blockquote,
figure,
ol,
ul {
  margin: 0;
  padding: 0; }

main,
li {
  display: block; }

h1,
h2,
h3,
h4 {
  font-size: inherit; }

strong {
  font-weight: bold; }

a,
button {
  color: inherit;
  transition: .3s; }

a {
  text-decoration: none; }

button {
  overflow: visible;
  border: 0;
  font: inherit;
  -webkit-font-smoothing: inherit;
  letter-spacing: inherit;
  background: none;
  cursor: pointer; }

::-moz-focus-inner {
  padding: 0;
  border: 0; }

:focus {
  outline: 0; }

img {
  display: block;
  max-width: 100%;
  height: auto;
  border: 0; }

div,
span,
ul,
li {
  box-sizing: border-box; }

.example-enter {
  opacity: 0.01;
  transition: opacity .5s ease-in; }
  .example-enter.example-enter-active {
    opacity: 1; }

.example-leave {
  opacity: 1;
  transition: opacity .5s ease-in; }
  .example-leave.example-leave-active {
    opacity: 0.01; }

.example-appear {
  opacity: 0.01;
  transition: opacity .5s ease-in; }

.example-appear.example-appear-active {
  opacity: 1; }
