.hamburger {
  background-color: white;
  cursor: pointer;
  display: block;
  height: 3em;
  transition: transform .8s ease;
  width: 3em; }
  .hamburger__bar {
    background-color: black;
    display: block;
    height: 4px;
    margin-left: -.7em;
    left: 50%;
    position: relative;
    top: 50%;
    transition: background-color 0s ease 0s;
    width: 1.4em; }
    .hamburger__bar::before {
      background-color: black;
      content: '';
      height: 4px;
      top: 7px;
      transition: transform .8s ease;
      position: absolute;
      width: 1.4em; }
    .hamburger__bar::after {
      background-color: black;
      content: '';
      height: 4px;
      position: absolute;
      transition: transform .8s ease;
      top: -7px;
      width: 1.4em; }
  .hamburger:active, .hamburger:hover {
    transform: rotate(180deg); }
    .hamburger:active .hamburger__bar, .hamburger:hover .hamburger__bar {
      background-color: transparent; }
    .hamburger:active .hamburger__bar::before, .hamburger:hover .hamburger__bar::before {
      top: -.2em;
      transform: rotate(-45deg); }
    .hamburger:active .hamburger__bar::after, .hamburger:hover .hamburger__bar::after {
      top: -.2em;
      transform: rotate(45deg); }
