.hero {
  height: 100vh;
  position: relative; }
  .hero__image {
    height: 100%;
    left: 0;
    object-fit: cover;
    position: absolute;
    top: 0;
    width: 100%; }
  .hero__title {
    bottom: 5rem;
    color: #ffffff;
    font-size: 2.64rem;
    left: 2rem;
    position: absolute;
    right: 2rem; }
  @media only screen and (min-width: 768px) {
    .hero {
      height: 125vh; }
      .hero__title {
        bottom: 35vh;
        max-width: 60rem;
        font-size: 5.52rem;
        left: 3rem;
        right: 3rem; } }
  @media only screen and (min-width: 1024px) {
    .hero__title {
      font-size: 7.2rem;
      left: 4rem;
      right: 4rem; } }
