.menu {
  background: #000000;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-around;
  margin: 0; }
  .menu__link {
    color: white;
    text-decoration: none;
    text-transform: uppercase; }
    .menu__link:hover {
      color: grey;
      transition: color 600ms; }
  @media (min-width: 768px) {
    .menu {
      align-items: center;
      flex-direction: row; } }
