.project {
  background-color: #000000;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding-bottom: 3.6rem;
  padding-left: 2rem;
  padding-right: 2rem;
  padding-top: 6rem;
  position: relative;
  width: 100%; }
  .project__image {
    height: 100%;
    left: 0;
    object-fit: cover;
    opacity: .9;
    position: absolute;
    top: 0;
    width: 100%; }
  .project__title {
    font-size: 2.124rem;
    line-height: 2.4rem;
    margin: 0;
    padding-bottom: 1.2rem;
    position: relative; }
  .project__blurb {
    font-size: 0.96rem;
    line-height: 1.2rem;
    padding-bottom: 1.2rem;
    position: relative; }
  .project__subtitle {
    line-height: 1.2rem;
    font-size: 0.96rem;
    position: relative; }
  .project__link {
    bottom: 0;
    left: 0;
    position: absolute;
    right: 0;
    top: 0; }
  @media (min-width: 768px) {
    .project {
      padding-left: 3rem;
      padding-right: 3rem; }
      .project__title {
        font-size: 2.64rem;
        line-height: 3.6rem; }
      .project__subtitle, .project__blurb {
        font-size: 1.2rem;
        line-height: 1.2rem; } }
  @media (min-width: 1024px) {
    .project {
      padding-bottom: 6rem; }
      .project__title {
        line-height: 3.6rem;
        font-size: 3.36rem; } }
  @media (min-width: 1200px) {
    .project {
      padding-right: 8rem; } }
